import {  InputItem } from 'antd-mobile';

export default React.createClass({
    render(){
        return (
            <div className="card_con">
                <div className="tip">
                    <i
                        style={{background:"url(images/images/icon_05.png) 0% 0% /100%"}}
                    ></i>
                    <span></span>
                </div>
                <div className="title">
                    <i
                    style={{background:"url(images/images/icon_10.png) 0% 0% /100%"}}
                    ></i>
                    <span></span>
                    <span>12000</span>
                </div>
                <div className="tip">
                    <i
                        style={{background:"url(images/images/icon_05.png) 0% 0% /100%"}}
                    ></i>
                    <span>(100-5000)</span>
                </div>
                <div className="title">
                    <span
                        style={{marginLeft:"0.36rem"}}
                    ></span>
                    <InputItem
                    style={{height:"0.42rem",fontSize:"0.28rem"}} 
                    placeholder=""
                    />
                </div>
                <div className="server">
                    <span>40</span>
                </div>
                <div className="tip">
                    <i
                        style={{background:"url(images/images/icon_05.png) 0% 0% /100%"}}
                    ></i>
                    <span></span>
                </div>
                <div className="title">
                    <i
                    style={{background:"url(images/images/icon_11.png) 0% 0% /100%"}}
                    ></i>
                    <span></span>
                    <span>7</span>
                </div>
                <div className="submit">
                    <button></button>
                    <div>
                        <label><input type="checkbox" /></label><a></a>
                    </div>
                </div>
            </div>
        )
    }
})


