import {Router,Route,hashHistory,IndexRedirect,browserHistory,IndexRoute} from "react-router";
import Rou from "./view/router/router";//
import Home from "./view/router/home/homeRouter";//
import My from "./view/router/my/myRouter";//
import Login from "./view/router/login/loginRouter";//
import Merchants from "./view/router/merchants/merchantsRouter";//
import Qr from "./view/router/qr/qrcodeRouter";//
import Information from "./view/router/information/informationRouter";//
import Perfect from "./view/router/prefect/perfectRouter";//
import Loan from "./view/router/loan/loanRouter";//
import Repayment from "./view/router/repayment/repaymentRouter";//
import Already from "./view/router/already/alreadyrepayRouter";//
import About from "./view/router/about/aboutRouter";//
import Card from "./view/router/creditcard/creditcard";//
import Withdraw from "./view/router/withdraw/withdrawRouter";//
import Coupon from "./view/router/coupon/couponRouter";//
import Meloan from "./view/router/meloan/merchantsRouter";//
import Memoney from "./view/router/memoney/somewordRouter";//
import Gvrp from "./view/router/gvrp/gvrpRouter";//
import Getcoupon from "./view/router/getcoupon/getcouponRouter";//
import Waitcoupon from "./view/router/waitcoupon/waitcouponRouter";//
import Believe from "./view/router/believe/believeRouter";//
import Tcsuccess from "./view/router/tcsuccess/tcRouter";//
import Txing from "./view/router/txing/txingRouter";//
import Bzhome from "./view/router/bzhome/bzhomeRouter";//
import Loginsuccess from "./view/router/loginsuccess/loginsuccessRouter";//
import Loanlist from "./view/router/loanlist/loanlist";//
import Makemoney from "./view/router/makemoneylist/makemoneylist";//
import Sqcg from "./view/router/sqcg/sqcgRouter";//
import Sqsb from "./view/router/sqsb/sqsbRouter";//
import Pmh from "./view/router/pmh/pmhRouter";//
import Ddd from "./view/router/ddd/dd"
export default React.createClass({
    enterMy(){
  
    },
    render:function(){
        return (
            <Router history={hashHistory}>
                <Route path="/" component={Rou}>
                    <IndexRedirect to="home"/>                   
                    <Route path="home" component={Home} />
                    <Route path="my" component={My} onEnter={this.enterMy} />
                    <Route path="login" component={Login} />
                    <Route path="merchants" component={Merchants} />
                    <Route path="qr" component={Qr} />
                    <Route path="information" component={Information} />
                    <Route path="perfect" component={Perfect} />
                    <Route path="loan" component={Loan} />
                    <Route path="repayment" component={Repayment} />
                    <Route path="already" component={Already} />
                    <Route path="about" component={About} />
                    <Route path="withdraw" component={Withdraw} />
               		<Route path="coupon" component={Coupon} />
                    <Route path="meloan" component={Meloan} />
                    <Route path="memoney" component={Memoney} />
                    <Route path="gvrp" component={Gvrp} />
                    <Route path="getcoupon" component={Getcoupon} />
                    <Route path="waitcoupon" component={Waitcoupon} />
                    <Route path="believe" component={Believe} />
                    <Route path="tcsuccess" component={Tcsuccess} />
                    <Route path="txing" component={Txing} />
                    <Route path="bzhome" component={Bzhome} />
                    <Route path="loginsuccess" component={Loginsuccess} />
                    <Route path="card" component={Card} />
                    <Route path="loanlist" component={Loanlist} />
                    <Route path="makemoney" component={Makemoney} />
                    <Route path="sqcg" component={Sqcg} />
                    <Route path="sqsb" component={Sqsb} />
                    <Route path="pmh" component={Pmh} />
                    <Route path="sqsb" component={Sqsb} />
                    <Route path="pmh" component={Pmh} />
                    <Route path="loanlist" component={Loanlist} />
                    <Route path="makemoney" component={Makemoney} />                   
                    <Route path="ddd" component={Ddd} />                   
                </Route>
            </Router>
        )
    }
})


