import {InputItem,ImagePicker} from "antd-mobile"



export default React.createClass({
    getInitialState(){
        return {
            files:[]
        }
    },
    onChange(files, type, index){
        // console.log(files[0].file)
        console.log(files, type, index);
        this.setState({
          files,
        });
      },
    render(){
        const {files}=this.state;
        return (
            <div className="withdrawcontent">
                <div className="title">
                    <div className="title_con">
                        <i
                            style={{background:"url(images/images/icon_04.png)",backgroundSize:"100%"}} 
                        ></i><span></span><span>12000</span>
                    </div>
                </div>
                <div className="tip">
                    <i
                        style={{background:"url(images/images/icon_05.png)",backgroundSize:"100%"}}
                    ></i>
                    (100-5000):
                </div>
                <div className="price">
                    <div className="top">
                        <span></span><InputItem 
                        style={{height:"0.42rem",fontSize:"0.28rem"}}
                        placeholder="" />
                    </div>
                    <div className="top">
                        <span></span><InputItem 
                        style={{height:"0.42rem",fontSize:"0.28rem"}}
                        placeholder="" />
                    </div>
                </div>
                <div className="fee">
                    40
                </div>
                <div className="company">
                <span></span><InputItem 
                        style={{height:"0.42rem",fontSize:"0.28rem"}}
                        placeholder="" />
                </div>
                <div className="tip day">
                    <i
                        style={{background:"url(images/images/icon_05.png)",backgroundSize:"100%"}}
                    ></i>
                    :<span>7</span>
                </div>
                <div className="tip">
                    <i
                        style={{background:"url(images/images/icon_05.png)",backgroundSize:"100%"}}
                    ></i>
                    
                </div>
                <div className="picker">
                    <ImagePicker
                    /* style={{borderColor:"red"}} */
                    files={files}
                    onChange={this.onChange}
                    onImageClick={(index, fs) => console.log(index, fs)}
                    selectable={files.length <1}
                    />
                </div>
                <div className="sub">
                    <input type="submit" value="" />
                    <div>
                        <input type="checkbox" />
                        <p><a></a></p>
                    </div>
                </div>
            </div>
        )
    }
})


